@use "global" as *;

.btn {
  display: inline-block;
  font-size: rem(20);
  padding: rem(10) rem(114);
  border: 1px solid #ffffff;
  color: $white;
  white-space: nowrap;
  @include mq("md") {
    font-size: vw(strip-unit($inner), 10);
    font-size: rem(16);
    padding: rem(3) rem(136);
    line-height: 3.375;
  }
}

// ホバーアニメーション用
.btn {
  position: relative;
  z-index: 0;
  overflow: hidden;
}
.btn {
  position: relative;
  display: inline-block;
  border: 1px solid #ffffff;
  color: #ffffff;
  text-align: center;
  text-decoration: none;
  transition: 0.3s;
}
.btn:hover {
  color: $black;
}
.btn::before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  content: "";
  background: #ffffff;
  transform-origin: right top;
  transform: scale(0, 1);
  transition: transform 0.3s;
}
.btn:hover::before {
  transform-origin: left top;
  transform: scale(1, 1);
}
